<template>
  <el-card class="box-card">
    <el-form ref="dataForm" :model="param" :rules="rule" label-width="120px">
      <el-form-item label="id" prop="id">
        <el-input v-model="param.id" placeholder="id"></el-input>
      </el-form-item>
      <el-form-item label="tag" prop="tag">
        <el-input type="number" v-model="param.tag" placeholder="tag"></el-input>
      </el-form-item>
      <el-form-item label="barcode" prop="barcode">
        <el-input v-model="param.barcode" placeholder="barcode"></el-input>
      </el-form-item>
      <el-form-item label="more_packages" prop="quantity">
        <el-input type="number" v-model="param.quantity" placeholder="more_packages"></el-input>
      </el-form-item>
      <el-form-item label="price" prop="price">
        <el-input type="number" v-model="param.price" placeholder="price"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">confirm</el-button>
      </el-form-item>
    </el-form>
  </el-card>
</template>

<script>
export default {
  data() {
    return {
      param: {
        id: '',
        tag: '',
        barcode: '',
        quantity: '',
        price: '',
      },
      rule: {
        id: [{required: true, message: "please fill in id", trigger: 'blur'}],
        tag: [{required: true, message: "please fill in tag", trigger: 'blur'}],
        barcode: [{required: true, message: "please fill in barcode", trigger: 'blur'}],
        quantity: [{required: true, message: "please fill in more_packages", trigger: 'blur'}],
        price: [{required: true, message: "please fill in price", trigger: 'blur'}],
      }
    }
  },
  methods: {
    onSubmit() {
      this.$refs.dataForm.validate((valid) => {
        if (valid) {
          this.$axios.post('/payload/create', this.param).then(res => {
            if (res.success) {
              this.$message({
                message: 'successfully added',
                type: 'success'
              });
              this.$router.back();
            } else {
              this.$message.error(res.message);
            }
          })
        }
      });
    }
  }
}
</script>

